
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


.content{
  height:100vh;
}

.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: rgb(220, 220, 220);

}

.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: rgb(220, 220, 220);

}
.log-regFormStyle{
  background-color: white;
    padding: 50px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
}
.log-reginheaderStyle{
  text-align: center;
  margin-bottom: 20px;
}

/* transection filters  */

.fliters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 5px #6dcff6;
}

/* ant design icons */

.anticon svg{
  font-size: 20px;
  cursor: pointer;

}

.active-icon{
  color: black;
}

.inactive-icon{
  color: grey;
}

.switch-icons{
  border: 1px solid rgba(0, 0, 0, 0.411);
  border-radius: 5px;
  padding: 5px 10px;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 20px;
  background-color: rgb(85, 83, 83);
  color: white;
  text-align: center;
}


.content {
  max-width: 100%; 
  margin: 0 auto; 
  padding: 20px; 
  box-sizing: border-box;
}

@media screen and (max-width: 768px) {
  
  .content {
    padding: 10px; 
  }
}


